<template>
  <div>
    <!-- <swiper :key="datalist.name">
      自处不能用ul li标签，看文档是div标签
      <div class="swiper-slide" v-for="data in datalist" :key="data.bannerId">
        <img
          :class="data.bannerId === 390 ? 'tang' : ''"
          :src="data.imgUrl"
          :alt="data.name"
        />
      </div>
    </swiper> -->
       <filmheader :class="isFixed?'fixed':''" ref="myfilm"></filmheader>
      <router-view></router-view>
      <foot></foot>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'
import swiper from '@/views/Swiper.vue'
import filmheader from '@/components/FilmHeader'
import Complaying from '@/views/Films/Comingsoon'
import Nowplaying from '@/views/Films/Nowplaying'
import Footer from '@/components/Footer'
Vue.component('swiper', swiper)
Vue.component('foot', Footer)
Vue.component('nowplaying', Nowplaying)
Vue.component('comingsoon', Complaying)
Vue.component('filmheader', filmheader)
export default {
  data () {
    return {
      datalist: [],
      nisShow: false,
      cisShow: true,
      isFixed: false
    }
  },
  methods: {
    handleChangeClick (e) {},
    handlescroll () {
      // console.log(this.$refs.myfilm.$el.offsetHeight) // swiper的高度
      // console.log(document.documentElement.scrollTop) // 鼠标滑动的高度
      if (document.documentElement.scrollTop >= 193) {
        this.isFixed = true
      } else {
        this.isFixed = false
      }
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?type=2&cityId=440300&k=5733205',
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"1615889219745679337029633"}',
        'X-Host': 'mall.cfg.common-banner'
      }
    }).then((res) => {
      this.datalist = res.data.data
    })
    window.onscroll = this.handlescroll
  },
  beforeDestory () {
    window.onscroll = null
  }
}
</script>
<style lang="scss" scoped>
.swiper-slide .tang {
  margin-top: 32px;
}
</style>
