<template>
  <div id="box">
    <ul class="ul">
      <li v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)" class="li">
        <img :src="data.poster" :alt="data.name" />
        <div>
          <span>{{ data.name + data.item.name }}</span>
          <span>观众评分:{{ data.grade }}</span>
          <span>主演:{{ data.director }}</span>
          <span>{{ data.nation }}|{{ data.runtime }}</span>
        </div>
        <div class="book">预购</div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      datalist: []
    }
  },
  mounted () {
    axios({
      url:
        'https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=2&k=2129513',
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"16159910672089342675714049","bc":"110100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then((res) => {
      // console.log('axios success! --->' + res.data.data)
      this.datalist = res.data.data.films
    })
  },
  methods: {
    handleChangePage (id) {
      // console.log(id)
      this.$router.push(`/film/${id}`)
      // 编程式导航-名字跳转-params
      // this.$router.push({name: 'comingsoonfilm',params:{id:id}})
      // 编程式导航-名字跳转-query
      // this.router.push({name: 'comingsoonfilm',query:{id:id}})
    }
  }
}
</script>
<style lang="scss" scoped>
  #box {
  width: 100%;
  .ul {
    list-style: none;
    margin: 0px;
    margin-left: 15px;
    padding: 0px;
    .li {
      position: relative;
      width: 100%;
      margin: 0px;
      list-style: none;
      clear: both;
      padding-top: 15px;
      padding-bottom: 15px;
      // padding-right: 15px;
      img {
        width: 66px;
        height: 94.73px;
        float: left;
      }
      div {
        span {
          display: block;
          overflow:hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
        span:nth-child(1){
          font-size: 16px;
          color: #191a1b;
        }
      }
      .book{
        position: absolute;
        right: 15px;
        top: 43px;
        width: 48px;
        height: 23px;
        border: 1px solid black;
        text-align: center;
       }
    }
  }
}
</style>
