<template>
  <div>
    <ul>
      <li v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)">
        <img :src="data.poster" :alt="data.name" />
        <div>
          <span>{{ data.name + data.item.name }}</span>
          <span>观众评分:{{ data.grade }}</span>
          <span v-if="data.director">主演:{{ data.director }}</span>
          <span v-else>暂无主演</span>
          <span>{{ data.nation}}|{{data.runtime}}</span>
        </div>
        <div class="byTicket">购票</div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      datalist: []
    }
  },
  mounted () {
    axios({
      url:
        'https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=1&k=9116520',
      // 'https://m.maizuo.com/gateway?cityId=411300&pageNum=1&pageSize=10&type=1&k=9965096',
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"16159910672089342675714049","bc":"110100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then((res) => {
      //   console.log(res.data.data.films[0])
      this.datalist = res.data.data.films
    })
    window.onscroll = function () {
      console.log(document.body.scrollHeight)
      console.log(document.scrollTop)
      // console.log()
    }
  },
  methods: {
    handleChangePage (id) {
      // console.log(id)
      // 编程式导航-路径跳转
      this.$router.push(`/film/${id}`)

      // 编程式导航-名字跳转-params
      // this.$router.push({
      //   name: 'nowplayingfilm',
      //   params: { id: id }
      // })

      // 编程式导航-名字跳转-query
      // this.$router.push({
      //   name: 'nowplayingfilm',
      //   query: { id: id }
      // })
    }
  }
}
</script>
<style lang="scss" scoped>
  div {
  width: 100%;
  ul {
    list-style: none;
    margin: 0px;
    margin-left: 15px;
    padding: 0px;
    li {
      position: relative;
      width: 100%;
      margin: 0px;
      list-style: none;
      clear: both;
      padding-top: 15px;
      padding-bottom: 15px;
      img {
        width: 66px;
        height: 94.73px;
        float: left;
      }
      div {
        span {
          display: block;
          overflow:hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
        span:nth-child(1){
          font-size: 16px;
          color: #191a1b;
        }
      }
      .byTicket{
        position: absolute;
        right: 15px;
        top: 43px;
        width: 48px;
        height: 23px;
        border: 1px solid black;
        text-align: center;
      }
    }
  }
}
</style>
